      <button v-if="!showWord" @click="revealWord" class="button">Ï†úÏãúÏñ¥ Î≥¥Í∏∞</button>
      <div v-if="showWord" class="word-display large">{{ currentWord }}</div>
      <button v-if="showWord" @click="nextPlayer" class="button">Îã§Ïùå Ï∞∏Í∞ÄÏûê</button>
    </div>
    <div v-else class="result-phase">
      <h2 class="selection-title">üîç ÌäπÎ≥ÑÏù∏ÏùÑ ÏÑ†ÌÉùÌïòÏÑ∏Ïöî!</h2>
      <ul class="player-list">
        <li v-for="(player, index) in players" :key="index" class="player-item">
          <button @click="selectLiar(index)" :disabled="selectedLiar !== null" class="button player-button">
            {{ index + 1 }}Î≤à Ï∞∏Í∞ÄÏûê
          </button>
        </li>
      </ul>
      <div v-if="selectedLiar !== null" class="result-message">
        <p class="result-text">{{ selectedLiar + 1 }}Î≤à Ï∞∏Í∞ÄÏûêÍ∞Ä ÌäπÎ≥ÑÏù∏ÏúºÎ°ú ÏßÄÎ™©ÎêòÏóàÏäµÎãàÎã§!</p>
        <p v-if="players[selectedLiar].isLiar" class="correct">üéâ Ï†ïÎãµ! ÌäπÎ≥ÑÏù∏ÏùÑ ÎßûÏ∑ÑÏäµÎãàÎã§!</p>
        <p v-else class="incorrect">üò¢ ÏïÑÏâΩÎÑ§Ïöî! ÌäπÎ≥ÑÏù∏Ïù¥ ÏïÑÎãôÎãàÎã§.</p>
        <button @click="resetGame" class="button reset">Îã§Ïãú ÌïòÍ∏∞</button>
