  data() {
    return {
      playerCount: 3,
      liarOption: "different",
      gameStarted: false,
      gameFinished: false,
      selectedCategory: "ÏÑ±Í≤ΩÏ±Ö",
      wordCategories: {
        "ÏÑ±Í≤ΩÏ±Ö": ["Ï∞ΩÏÑ∏Í∏∞", "Î†àÏúÑÍ∏∞", "ÎØºÏàòÍ∏∞", "Ïã†Î™ÖÍ∏∞", "Ïó¨Ìò∏ÏàòÏïÑ", "ÏÇ¨ÏÇ¨Í∏∞", "Î£ªÍ∏∞",
                  "ÏÇ¨Î¨¥ÏóòÏÉÅ/Ìïò", "Ïó¥ÏôïÍ∏∞ÏÉÅ/Ìïò", "Ïó≠ÎåÄÍ∏∞ÏÉÅ/Ìïò","ÏóêÏä§Îùº","ÎäêÌó§ÎØ∏Ïïº","ÏóêÏä§Îçî",
                  "Ïö•Í∏∞","ÏãúÌé∏","Ïû†Ïñ∏","Ï†ÑÎèÑÏÑú","ÏÜîÎ°úÎ™¨Ïùò ÎÖ∏Îûò (ÏïÑÍ∞Ä)","Ïù¥ÏÇ¨Ïïº","ÏòàÎ†àÎØ∏Ïïº",
                  "Ïï†Í∞Ä","ÏóêÏä§Í≤î","Îã§ÎãàÏóò","Ìò∏ÏÑ∏ÏïÑ","ÏöîÏóò","ÏïÑÎ™®Ïä§","Ïò§Î∞îÎåú","ÏöîÎÇò","ÎØ∏Í∞Ä",
                  "ÎÇòÌõî","ÌïòÎ∞ïÍµ≠","Ïä§Î∞îÎÉê","ÌïôÍ∞ú","Ïä§Í∞ÄÎû¥","ÎßêÎùºÍ∏∞","ÎßàÌÉú","ÎßàÍ∞Ä","ÎàÑÍ∞Ä",
                  "ÏöîÌïú","ÏÇ¨ÎèÑ","Î°úÎßà","Í≥†Î¶∞ÎèÑÏ†Ñ/ÌõÑ","Í∞àÎùºÎîîÏïÑ","ÏóêÎ≤†ÏÜå","ÎπåÎ¶ΩÎ≥¥","Í≥®Î°úÏÉà",
                  "Îç∞ÏÇ¥Î°úÎãàÍ∞ÄÏ†Ñ/ÌõÑ","ÎîîÎ™®Îç∞Ï†Ñ/ÌõÑ","ÎîîÎèÑÏÑú","ÎπåÎ†àÎ™¨","ÌûàÎ∏åÎ¶¨","ÏïºÍ≥†Î≥¥","Î≤†ÎìúÎ°úÏ†Ñ/ÌõÑ",
                  "ÏöîÌïú1,2,3ÏÑú","Ïú†Îã§","Í≥ÑÏãúÎ°ù"],
        "Ïù∏Î¨º": ["ÏïÑÎã¥", "ÌïòÏôÄ", "ÎßàÌÉú", "ÎßàÍ∞Ä", "ÎàÑÍ∞Ä", "ÏöîÌïú", "ÏÇ¨Î¨¥Ïóò", "Î°Ø", "Î£ª", "Ïù¥ÏÇ¨Î≤®"],
        "ÏßÅÏóÖ": ["ÏùòÏÇ¨", "ÏÑ∏Í∏àÏßïÏàòÏõê", "Ïñ¥Î∂Ä", "Ï†úÏÇ¨Ïû•"],
        "ÎèôÎ¨º": ["ÏÇ¨Ïûê", "Î±Ä", "ÎπÑÎëòÍ∏∞", "Îã≠", "Î©îÎöúÍ∏∞"],
        "ÏßÄÎ™Ö": ["Î°úÎßà", "Í∑∏Î¶¨Ïä§", "ÎØ∏Íµ≠", "Îü¨ÏãúÏïÑ"],
        "ÏÑ±Ìíà": ["Ïò®Ìôî", "ÏßÄÌòú"],
        "ÏÇ¨Í±¥": ["ÌôçÌï¥", "Ìò∏ÎûëÏù¥Íµ¥"],
        "Ï†àÍ∏∞": ["Ï£ºÏùòÎßåÏ∞¨", "Ïò§ÏàúÏ†à"],
        "ÎèÑÍµ¨": ["ÏïÑÎ°†Ïùò ÏßÄÌå°Ïù¥", "Í∂§"],
        "Ï≤úÏÇ¨": ["Í∞ÄÎ∏åÎ¶¨Ïóò", "Í∑∏Î£π"],
        "ÏòàÏñ∏": ["Ïñ¥Î¶∞Ïñë", "Ìè¨ÎèÑÎÇòÎ¨¥", "Î≥Ñ"],
        "ÏùåÏãù": ["ÎßåÎÇò", "ÏÑ†ÏïÖÍ≥º", "Ìè¨ÎèÑ", "Î¨¥ÌôîÍ≥º"]
      },
      players: [],
      currentPlayerIndex: 0,
      showWord: false,
      currentWord: "",
      selectedLiar: null
    };
  },
  methods: {
    validatePlayerCount(event) {
      const value = event.target.value.replace(/\D/g, '');
      this.playerCount = value ? Math.max(3, parseInt(value)) : 3;
    },
    startGame() {
      const selectedWords = this.wordCategories[this.selectedCategory];
      let commonWordIndex = Math.floor(Math.random() * selectedWords.length);
      let commonWord = selectedWords[commonWordIndex];
      let liarWord;
      
      do {
        liarWord = selectedWords[Math.floor(Math.random() * selectedWords.length)];
      } while (liarWord === commonWord);
      
      const liarIndex = Math.floor(Math.random() * this.playerCount);
      this.players = Array.from({ length: this.playerCount }, (_, i) => ({
        id: i,
        isLiar: i === liarIndex,
        word: i === liarIndex ? (this.liarOption === "different" ? liarWord : "?") : commonWord
      }));
      
      this.gameStarted = true;
      this.gameFinished = false;
      this.currentPlayerIndex = 0;
      this.showWord = false;
      this.selectedLiar = null;
    },
    revealWord() {
      this.currentWord = this.players[this.currentPlayerIndex].word;
      this.showWord = true;
    },
    nextPlayer() {
      this.showWord = false;
      if (this.currentPlayerIndex < this.playerCount - 1) {
        this.currentPlayerIndex++;
      } else {
        this.gameFinished = true;
        setTimeout(() => alert("Î™®Îì† Ï∞∏Í∞ÄÏûêÍ∞Ä Ï†úÏãúÏñ¥Î•º ÌôïÏù∏ÌñàÏäµÎãàÎã§. ÌäπÎ≥ÑÏù∏ÏùÑ ÏÑ†ÌÉùÌïòÏÑ∏Ïöî!"), 100);
      }
    },
    selectLiar(index) {
      this.selectedLiar = index;
    },
    resetGame() {
      this.gameStarted = false;
      this.gameFinished = false;
      this.selectedLiar = null;
    }
  }
};
</script>

